<template>
<div class="prueba">
    <div class="title"><p>Choose a date</p></div>
    
    <div class="everything">
        <div class="tarjeta">
            <router-link v-bind:to="artistURI"><img v-bind:src="artistImage" class="card-img-top artistImage" alt="Artist's Image"></router-link>
            <div class="card-body cuerpoTarjeta">
                <div class="leftContent">
                    <h5 class="card-title artistName">{{ artistName }}</h5>
                    <span class="card-text artistGenres">{{ genresToString() }}</span>
                </div>
                <div class="rightContent">
                    <p class="price">{{ price }}</p>
                </div>
            </div>
        </div>
        <div class="calendarButton">
          <div class="calendar"><Calendar @datePickerDate="calendarSelected" :availableDates="this.datos[0].availableDates"/></div>
          <div class="continueButtonDiv"><div @click="dateSelected()" class="btn btn-primary continueButton"><span class="continueText">CONTINUE</span></div></div>
        </div>
    </div>
</div>
</template>

<script>
import Calendar from '@/components/Calendar.vue';
import GAxios from '@/utils/GAxios.js';
import GSecurity from '@/security/GSecurity.js';
import endpoints from '@/utils/endpoints.js';

export default {
  name: 'DateSelection',

  components: {
    Calendar,
  },

  data: function() {
      return {
          gsecurity: GSecurity,
          datos: Array(),
          fecha: '',
      }
  },

  beforeMount: function() {
      var authorizedGAxios = GAxios;
      var GAxiosToken = this.gsecurity.getToken();
      authorizedGAxios.defaults.headers.common['Authorization'] = 'Token ' + GAxiosToken;

      authorizedGAxios.get('/artist' + endpoints.calendar + this.$route.params['artistId'] + '/')
        .then(response => {
            var calendar = response.data;
            //console.log(calendar[0].days)

            this.datos.push({
                availableDates: calendar[0].days,
            })

        }).catch(ex => {
            //console.log(ex);
        });

  },

  props: {
        artistURI: {
            type: String,
            default: 'showPortfolio'
        },
        artistImage: {
            type: String,
            default: 'http://www.tiumag.com/wp-content/uploads/rosalia-2018-2-705x564.jpg',
        },
        artistName: {
            type: String,
            default: 'ROSALÍA'
        },
        artistGenres: {
            type: Array,
            default: ['Pop', 'Flamenco']
        },
        continueURI: {
            type: String,
            default: 'timeSelection'
        },
        price: {
          type: String,
          default: '$63.00/h'
        }
    },

    methods: {
        genresToString() {

            var res = "";
            var i = 0;

            for (i = 0; i < this.artistGenres.length; i++) { 
                if (i != this.artistGenres.length - 1) {
                    res += this.artistGenres[i] + ", ";
                } else {
                    res += this.artistGenres[i];
                }
            }

            return res;
        },

        dateSelected() {
            this.$emit('dateSelected', this.fecha);
        },

        calendarSelected(){
            this.fecha = arguments[0];
        }
    }
}
</script>

